export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			hidden: true,
			listCurID: '',
			list: [],
			listCur: '',
			CustomBar: 0,
			cityList:[],
			keys:[],
			values:[],
			checked:false
		};
	},
	onLoad() {
		
		this.listCur = this.keys;
		
		uni.request({
			url: this.websiteUrl + '/api/getAllCity',
			method: 'GET',
			dataType: 'json',
			success: (res) => {
				console.log('获取所有的城市', res);
				this.cityList = res.data.data;
				var map = new Map();
				var val = [];
				this.cityList.forEach(item => {
					if (!map[item.cityFirstCode]) {
						val.push(item)
						map[item.cityFirstCode] = val;
						val = [];
					} else {
						map[item.cityFirstCode].push(item);
					}
				})
				this.cityList =map;
				this.keys = Object.keys(this.cityList);
				this.values = Object.values(this.cityList);
				
				
			}
		})
	},
	onReady() {
		let that = this;
		uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
			that.boxTop = res.top
		}).exec();
		uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
			that.barTop = res.top
		}).exec()
		console.log('that.boxTop',that.boxTop);
	},
	methods: {
		//获取文字信息
		getCur(e) {
			// console.log('11111111111',e)
			this.hidden = false;
			this.listCur = e.target.id;
			// console.log('this.listCur',this.listCur);
		},
		setCur(e) {
			// console.log('222222222',e)
			this.hidden = true;
			this.listCur = this.listCur
		},
		//滑动选择Item
		tMove(e) {
			console.log('move========',e);
			let y = e.touches[0].clientY,
				offsettop = this.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				this.listCur = that.cityList[num];
				console.log('that.cityList[num]',that.cityList[num]);
			};
		},
		//触发全部开始选择
		tStart() {
			this.hidden = false
		},
		//触发结束选择
		tEnd() {
			this.hidden = true;
			this.listCurID = this.listCur
		},
		indexSelect(e) {
			let that = this;
			let barHeight = this.barHeight;
			let list = this.list;
			let scrollY = Math.ceil(list.length * e.detail.y / barHeight);
			for (let i = 0; i < list.length; i++) {
				if (scrollY < i + 1) {
					that.listCur = list[i].name;
					that.movableY = i * 20
					return false
				}
			}
		},
		selectCity(item){
			this.checked = true;
			console.log(item);
			uni.switchTab({
				url:'../index/index'
			})
		}
	}
}
